import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";

import Cv from "./pages/Cv";

import "./App.css";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/resume" exact component={Cv} />
        <Route path="*404" exact component={Cv} />
        <Redirect to="/resume" />
      </Switch>
    </Router>
  );
}

export default App;
